.right-bar {
  background-color: white;
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  display: block;
  position: fixed;
  transition: all 200ms ease-out;
  width: 18rem;
  z-index: 9999;
  float: right !important;
  right: -(18rem + 0.6rem);
  top: 0;
  bottom: 0;

  .rightbar-title {
    .right-bar-toggle {
      height: 1.5rem;
      width: 1.5rem;
      line-height: 24px;
      color: rgb(43, 42, 42);
      text-align: center;
      border-radius: 50%;
      cursor: pointer;
    }
    .wish-list {
      font-size: 1rem;
      color: #222222;
      font-weight: 700;
      text-transform: uppercase;
      margin-bottom: 0;
    }
  }
  .right-bar-item {
    align-items: center;
    .right-bar-item-image {
      width: 5rem;
      height: 5rem;
      padding: 0.5rem;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .wishlist_link {
    cursor: pointer;
    letter-spacing: 1px;
    position: relative;
    text-decoration: none;
    padding: 12px 25px;
    border-radius: 50px;
    border: none;
    overflow: hidden;
    background-color: #ffb50d;
    color: white;
    font-size: 1rem;
    margin: 2rem 3.5rem;
  }
}

// Rightbar overlay
.rightbar-overlay {
  background-color: rgba(black, 0.55);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  z-index: 9998;
  transition: all 0.2s ease-out;
}

.right-bar-enabled {
  .right-bar {
    right: 0;
  }

  .rightbar-overlay {
    display: block;
  }
}

